* {
  font-family: "Montserrat", sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}



/*button color types*/

:root {
  --primary: #4628ff;
  --warning: #ffd028;
  --danger: #eb3f27;
  --success: #75fa83;
  --white: #fdfdfd;
  --dark: #181717;
 
}
/*button properties*/

.btn {
  font-family: "Montserrat", sans-serif;
  align-items: center;
  font-weight: 400;
  border-radius: 25px;
  cursor: pointer;
  transition: transform 0.3s ease;
  outline: none;
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.1);


  
}


.btn:not(:focus):hover {
  transform: translateY(-3px);
  transform: scale(1.2);
}
.btn:focus{
  opacity: 1;
}
/*button colors*/

.btn--primary--solid {
  background-color: #fdfdfd;
  background-color: var(--white);
  color: #181717;
  color: var(--dark);
  border: black;
}

.btn--warning--solid {
  background-color: #ffd028;
  background-color: var(--warning);
  color: #181717;
  color: var(--dark);
  border: none;
}
.btn--danger--solid {
  background-color: #00abeb;
  color: #fdfdfd;
  color: var(--white);
  border: none;
}
.btn--success--solid {
  background-color: #75fa83;
  background-color: var(--success);
  color: #fdfdfd;
  color: var(--white);
  border: none;
}
.btn--primary--outline {
  background-color: transparent;
  border: 2px solid #4628ff;
  border: 2px solid var(--primary);
  color: #4628ff;
  color: var(--primary);
}
.btn--warning--outline {
  background-color: transparent;
  border: 2px solid #ffd028;
  border: 2px solid var(--warning);
  color: #ffd028;
  color: var(--warning);
}
.btn--danger--outline {
  background-color: transparent;
  border: none;
  color: #181717;
  color: var(--dark);
}
.btn--success--outline {
  background-color: transparent;
  border: 2px solid #75fa83;
  border: 2px solid var(--success);
  color: #75fa83;
  color: var(--success);
}

/*button sizes*/

.btn--medium {
  height: 30px;
  width: 80px;
  margin-top: 10px;
  float: right;
  margin-right: 20px;
  font-size: 20px;
  

}

.btn--large {
  height: 260px;
  width: 350px;
  padding-bottom: 17px;
  font-size: 40px;
  
}

:root {
  --bg: #242526;
  --bg-accent: #484a4d;
  --text-color: black;
  --nav-size: 40px;
  --border-radius: 8px;
  --speed: 500ms;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  color: black;
  color: var(--text-color);
  text-decoration: none;
}

.navbar-nav {
  max-width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
}

.nav-item {
  width: 40px;
  width: var(--nav-size);
  height: 40px;
  height: var(--nav-size);
  display: flex;
  align-items: center;
  justify-content: center;
}
.icon-button {
  --button-size: calc(var(--nav-size) * 0.5);
  width: var(--button-size);
  height: var(--button-size);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  transition: -webkit-filter 300ms;
  transition: filter 300ms;
  transition: filter 300ms, -webkit-filter 300ms;
}
.icon-button-small {
  --button-size: calc(var(--nav-size) * 0.5);
  width: var(--button-size);
  height: var(--button-size);
  border-radius: 50%;
  transition: -webkit-filter 300ms;
  transition: filter 300ms;
  transition: filter 300ms, -webkit-filter 300ms;
}
.logout-icon {
  display: flex;
  width: 30px;
  margin: 0% 20px 0% 15px;
  align-items: flex-end;
}
.icon-circle {
  width: calc(var(--button-size) * 1.5);
  height: calc(var(--button-size) * 1.5);
  background-color: #f8f8f8ee;
  border: 2px solid rgb(0, 132, 255);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: -webkit-filter 300ms;
  transition: filter 300ms;
  transition: filter 300ms, -webkit-filter 300ms;
}
.icon-circle-small {
  height: 40px;
  width: 40px;
  background-color: #f8f8f8ee;
  border: 2px solid rgb(0, 132, 255);
  display: flex;
  align-items: center;
  border-radius: 50%;
  justify-content: center;
  transition: -webkit-filter 300ms;
  transition: filter 300ms;
  transition: filter 300ms, -webkit-filter 300ms;
  margin: 0% 0% 30% 15%;
}
.icon-button:hover {
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
}
.icon-button-small:hover {
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
}

.icon-circle:hover {
  cursor: pointer;
}
.menu-dropdown {
  position: absolute;
  top: 58px; /*depende sa height sa navbar*/
  transform: translateX(-42%);
  background-color: white;
  border-radius: 8px;
  border-radius: var(--border-radius);
  overflow: hidden;
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.1);
}
.logout-menu {
  display: flex;
  justify-content: left;
  height: 40px;
  transition: background 500ms;
  transition: background var(--speed);
  border-radius: 0 0 8px 8px;
  border-radius: 0 0 var(--border-radius) var(--border-radius);
  padding: 0.2rem;
  border-top: 2px solid rgb(158, 158, 158);
  font-weight: bold;
}
.profile-menu {
  height: 75px;
  display: flex;
  align-items: center;
  border-radius: 8px 8px 0 0;
  border-radius: var(--border-radius) var(--border-radius) 0 0;
  transition: background 500ms;
  transition: background var(--speed);
  padding: 0.2rem;
}
.profile-menu:hover,
.logout-menu:hover {
  background-color: #d6d1d1c0;
}
.name {
  height: 50%;
  font-size: x-large;
  font-weight: bold;
}
.icon-space {
  height: 75%;
  width: 60px;
  margin: 3% 5px 3% 5px;
}
.name-space {
  height: 80%;
  margin: 1% 5% 0% 5px;
  padding-right: 20px;
}
.description {
  font-size: small;
  height: 40%;
  margin-top: 1%;
}
.logout-text {
  margin-top: 7px;
}

.menu-dropdown {
  visibility: visible;
  animation: fadeIn 0.5s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.logout-text {
  color: black;
}

